/* checkbox */
.toggle-checkbox,
.toggle-radio {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 0;
  cursor: pointer;
  line-height: 2;
}
.toggle-checkbox[disabled],
.toggle-radio[disabled]  {
  cursor: no-drop;
}
/* 选择框 */
.toggle-checkbox .box_outline,
.toggle-radio .box_outline {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  width: 1.2em;
  height: 1.2em;
  margin: 0;
  cursor: pointer;
  /*overflow: hidden;*/
  border: 2px solid rgba(0,0,0,.54);
  border-radius: 0.1em;
}
.toggle-checkbox .tick_outline,
.toggle-radio .tick_outline {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: 0 0;
  transition-duration: .28s;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  /*transition-property: background;*/
  mask: url("data:image/svg+xml;base64,");
}
/* 标签 */
.toggle-checkbox .toggle-label,
.toggle-radio .toggle-label {
  margin: 0;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
}
/* 选中 */
.toggle-checkbox[checked] .box_outline,
.toggle-radio[checked] .box_outline {
  border-color: #3f51b5;
}
.toggle-checkbox[checked] .tick_outline,
.toggle-radio[checked] .tick_outline {
  background: url("data:image/svg+xml;base64,");
  background-color: #3f51b5;
}
/* 禁用 */
.toggle-checkbox[disabled] .box_outline,
.toggle-radio[disabled] .box_outline {
  border-color: #ddd;
  pointer-events: none;
}
.toggle-checkbox[disabled] .toggle-label,
.toggle-radio[disabled] .toggle-label {
  cursor: no-drop;
}
/* 选中禁用 */
.toggle-checkbox[disabled].toggle-checkbox[checked] .tick_outline,
.toggle-radio[disabled].toggle-radio[checked] .tick_outline {
  background-color: #ddd;
  pointer-events: none;
}





/* switch */
.toggle-switch {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 0;
  cursor: pointer;
  position: relative;
}
/* 轨道 */
.toggle-switch .track {
  display: inline-block;
  vertical-align: middle;
  width: 2.5em;
  height: 1em;
  border-radius: 1em;
  background: rgba(0,0,0,.26);
  transition-duration: .28s;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
}
/* 球 */
.toggle-switch .thumb {
  position: absolute;
  background: #fafafa;
  left: 0;
  top: 0.1em;
  width: 1.3em;
  height: 1.3em;
  border-radius: 50%;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
  transition-duration: .28s;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
}

/* 选中 */
.toggle-switch[checked] .track {
  background: rgba(63,81,181,.5);
}
.toggle-switch[checked] .thumb {
  left: calc(100% - 1.3em);
  /*left: inherit;
  right: 0;*/
  background: rgba(63,81,181,1);
}
/* 禁用 */
.toggle-switch[disabled] {
  cursor: no-drop;
}
.toggle-switch[disabled] .track,
.toggle-switch[disabled] .thumb {
  background-color: #ddd;
  pointer-events: none;
  cursor: no-drop;
}

/* switch-text */
.toggle-switch.switch-text .track {
  width: auto;
  height: auto;
  /*padding: 0.1em 1.1em;*/
  padding: 0.1em 0.7em 0.1em 1.5em;
}
.toggle-switch.switch-text .thumb {
  top: 0.11em;
}
.toggle-switch.switch-text[checked] .track {
  padding: 0.1em 1.5em 0.1em 0.7em;
}




/* radio 其实就是将checkbox用了50%的圆角*/
.toggle-radio .box_outline {
  border-radius: 50%;
}
.toggle-radio .tick_outline {
  transform: scale(0);
}
.toggle-radio[checked] .tick_outline {
  background-image: none;
  /*width: 80%;
  height: 80%;
  top: 10%;
  left: 10%;*/
  transform: scale(1);
  width: 100%;
  height: 100%;
  border: 2px solid #fff;
  box-sizing: border-box;
  border-radius: 50%;
}